.faqs .faq {
    margin: 15px;
    margin-top: 45px;
    border-radius: 2rem;
    padding: 25px;

    background-color: #2d2d2d;
    box-shadow: 0px 0px 25px -10px #3a0ca3;

    color: #FFFFFF;
    font-family: "Baloo 2"; 
    
}

.faqs .faq .faq-question {
    position: relative;
    font-size: 25px;
    letter-spacing: 2px; 
    padding-right: 80px;

    transition: all 0.4s ease;
}



.faqs .faq .faq-question::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-50%);
    width: 30px;
    height: 30px;


    background-image: url('./components/assets/down-arrow-svgrepo-com (1).svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-reapeat;

    transition: all 0.4s ease;
}

.faqs .faq .faq-answer {
    opacity: 0;
    max-height: 0;
    overflow-y: hidden;
    transition: all 0.6s ease-out;

}

.faqs .faq.open .faq-question {
    margin-bottom: 25px;
}

.faqs .faq.open .faq-question::after {
    transform: translateY(-50%) rotate(180deg);
}

.faqs .faq.open .faq-answer {
    max-height: 1000px;
    opacity: 1;
}


.css-selector {
    background: linear-gradient(-45deg, #37829f, #ae71ae, #2f57cf, #cf63c4);
    background-size: 800% 800%;
    animation: AnimationName 36s ease infinite;
    width: 100%;
    height: 100%;
    
}

@keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}



